import PropTypes from 'prop-types';
import { useNavigate } from 'react-router-dom';
import AddOutlinedIcon from '@mui/icons-material/AddOutlined';
import NavigateNextIcon from '@mui/icons-material/NavigateNext';
import CardActions from '@mui/material/CardActions';

export default function EntryActions({ createPath, viewPath }) {
  const navigate = useNavigate();
  return (
    <CardActions disableSpacing sx={{ py: 0, height: 'calc(15%)' }}>
      <AddOutlinedIcon
        onClick={() => {
          navigate(createPath);
        }}
        sx={{
          mx: 1,
          border: 2,
          borderRadius: '50%',
          fontSize: { xs: '2rem', xl: '2.5rem' },
          color: 'gray',
          '&:hover': {
            border: 3,
          },
        }}
      />
      <NavigateNextIcon
        onClick={() => {
          navigate(viewPath);
        }}
        sx={{
          m: 1,
          border: 2,
          borderRadius: '50%',
          fontSize: { xs: '2rem', xl: '2.5rem' },
          color: 'gray',
          marginLeft: 'auto',
          '&:hover': {
            border: 3,
          },
        }}
      />
    </CardActions>
  );
}

EntryActions.propTypes = {
  createPath: PropTypes.string.isRequired,
  viewPath: PropTypes.string.isRequired,
};
